import React, { useState, useContext, useEffect } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * UrlForm component for handling URL input and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered URL input form.
 */
export const UrlForm = () => {
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);
  const [url, setUrl] = useState(formData.url?.link || "");
  const { t } = useTranslation();

  useEffect(() => {
    if (formData.url?.link) {
      setUrl(formData.url.link);
    }
  }, [formData.url?.link]);

  useEffect(() => {
    if (url.trim() !== "") {
      setQrCodeVisible(true);
      const newUrlData = { link: url };

      if (formData.url?.link !== url) {
        const qrCodeValue = generateQRCodeValue({ url: newUrlData });
        updateFormData("url", { ...newUrlData, qrCodeValue });
        setQrCodeVisible(true);
      }
    } else {
      setQrCodeVisible(false);
    }
  }, [url, formData.url, updateFormData, setQrCodeVisible]);

  const handleUrlChange = (e) => {
    setUrl(e.target.value);
  };

  return (
    <form className="row">
      <div className="input-field col s12">
        <input
          type="url"
          id="url-input"
          placeholder={t("form.urlPlaceholder")}
          value={url}
          required
          onChange={handleUrlChange}
        />
      </div>
    </form>
  );
};
