import React, { useState, useContext, useEffect } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * TextForm component for handling text input and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered text input form.
 */
export const TextForm = () => {
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);
  const [text, setText] = useState(formData.text?.text || "");
  const { t } = useTranslation();

  useEffect(() => {
    if (formData.text?.text) {
      setText(formData.text.text);
    }
  }, [formData.text?.text]);

  useEffect(() => {
    if (text.trim() !== "") {
      setQrCodeVisible(true);
      const newTextData = { text };

      if (formData.text?.text !== text) {
        updateFormData("text", newTextData);
        const qrCodeValue = generateQRCodeValue({ text: newTextData });
        updateFormData("text", { ...newTextData, qrCodeValue });
      }
    } else {
      setQrCodeVisible(false);
    }
  }, [text, updateFormData, setQrCodeVisible]);

  return (
    <form className="row">
      <div className="input-field col s12">
        <input
          type="text"
          id="text-input"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder={t("form.placeholder")}
          required
        />
      </div>
    </form>
  );
};
