import React, { useContext } from "react";
import { QRCodeContext } from "../context/QRCodeContext";
import { Container } from "@mui/material";
import config from "../config/config";

/**
 * Component for uploading a logo image to be embedded in the QR code.
 *
 * @component
 * @returns {JSX.Element} The file upload interface for the logo.
 */
const QRCodeLogoUploader = () => {
  const { setLogoImage } = useContext(QRCodeContext);

  const handleLogoUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => setLogoImage(e.target.result);
      reader.readAsDataURL(file);
    }
  };

  return (
    <Container>
      <div className="file-field input-field">
        <div className={`btn ${config.theme.primaryColor} darken-2`}>
          <span>Upload Logo</span>
          <input type="file" accept="image/*" onChange={handleLogoUpload} />
        </div>
        <div className="file-path-wrapper">
          <input
            className="file-path validate"
            type="text"
            placeholder="Upload your logo"
          />
        </div>
      </div>
    </Container>
  );
};

export default QRCodeLogoUploader;
