import React, { useContext } from "react";
import { QRCodeContext } from "../context/QRCodeContext";
import "materialize-css/dist/css/materialize.min.css";
import {
  getColorLabel,
  getCurrentColorByType,
  updateColorByType,
} from "../utils/colorUtils";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

/**
 * Component for selecting and updating the color of the QR code or background.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {string} props.colorType - The type of color being selected (e.g., QR color, background color).
 * @returns {JSX.Element} The color input field for selecting colors.
 */
const ColorInput = ({ colorType }) => {
  const {
    qrColor,
    setQrColor,
    qrBackgroundColor,
    setQrBackgroundColor,
    gradientColor,
    setGradientColor,
    isTransparentBackground,
  } = useContext(QRCodeContext);

  const { t } = useTranslation();

  const handleChange = (e) => {
    updateColorByType(
      colorType,
      e.target.value,
      setQrColor,
      setQrBackgroundColor,
      setGradientColor,
    );
  };

  const currentColor = getCurrentColorByType(
    colorType,
    qrColor,
    qrBackgroundColor,
    gradientColor,
  );

  return (
    <div className="input-field" style={{ marginTop: "20px" }}>
      <input
        type="color"
        id="color-input"
        value={isTransparentBackground ? "transparent" : currentColor}
        onChange={handleChange}
        style={{
          appearance: "none",
          width: "100%",
          height: "40px",
          backgroundColor: "transparent",
          border: "none",
          cursor: "pointer",
          borderRadius: "35px",
          marginTop: "10px",
          padding: 0,
        }}
      />
      <label htmlFor="color-input" className="active">
        {t(getColorLabel(colorType))}
      </label>
    </div>
  );
};

ColorInput.propTypes = {
  colorType: PropTypes.string.isRequired,
};

export default ColorInput;
