/**
 * Gets the label for a color type.
 *
 * @param {string} colorType - The type of color ("foreground", "background", or "gradient").
 * @returns {string} - The corresponding label for the color type.
 */
export const getColorLabel = (colorType) => {
  switch (colorType) {
    case "foreground":
      return "color.foreground";
    case "background":
      return "color.background";
    case "gradient":
      return "color.gradient";
    default:
      return "color.default";
  }
};

/**
 * Updates the color based on the specified type.
 *
 * @param {string} colorType - The type of color to update.
 * @param {string} color - The new color value.
 * @param {function} setQrColor - Function to update the QR foreground color.
 * @param {function} setQrBackgroundColor - Function to update the QR background color.
 * @param {function} setGradientColor - Function to update the gradient color.
 */
export const updateColorByType = (
  colorType,
  color,
  setQrColor,
  setQrBackgroundColor,
  setGradientColor,
) => {
  switch (colorType) {
    case "foreground":
      setQrColor(color); // Updates the foreground color
      break;
    case "background":
      setQrBackgroundColor(color); // Updates the background color
      break;
    case "gradient":
      setGradientColor(color); // Updates the gradient color
      break;
    default:
      break;
  }
};

/**
 * Retrieves the current color based on the specified color type.
 *
 * @param {string} colorType - The type of color to retrieve.
 * @param {string} qrColor - The current QR foreground color.
 * @param {string} qrBackgroundColor - The current QR background color.
 * @param {string} gradientColor - The current gradient color.
 * @returns {string} - The current color value.
 */
export const getCurrentColorByType = (
  colorType,
  qrColor,
  qrBackgroundColor,
  gradientColor,
) => {
  switch (colorType) {
    case "foreground":
      return qrColor; // Returns the foreground color
    case "background":
      return qrBackgroundColor; // Returns the background color
    case "gradient":
      return gradientColor; // Returns the gradient color
    default:
      return "#FFFFFF"; // Default color if no match is found
  }
};
