import React, { useContext } from "react";
import QRCodeDisplay from "../components/QRCodeDisplay";
import QRCodePlaceholder from "../components/QRCodePlaceholder";
import { QRCodeContext } from "../context/QRCodeContext";
import PropTypes from "prop-types";

/**
 * Component for displaying the QR code section, showing either the generated
 * QR code or a placeholder depending on visibility.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {Object} props.canvasRef - Reference to the canvas element.
 * @returns {JSX.Element} The QR code display or placeholder.
 */
const QRCodeDisplaySection = ({ canvasRef }) => {
  const { qrCodeVisible } = useContext(QRCodeContext);

  return (
    <div className="col s12 m4">
      {qrCodeVisible ? (
        <QRCodeDisplay canvasRef={canvasRef} />
      ) : (
        <QRCodePlaceholder />
      )}
    </div>
  );
};

// PropTypes validation
QRCodeDisplaySection.propTypes = {
  canvasRef: PropTypes.object.isRequired,
};

export default QRCodeDisplaySection;
