import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * WifiForm component for handling Wi-Fi input fields and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered Wi-Fi input form.
 */
export const WifiForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [ssid, setSsid] = useState(formData.wifi?.ssid || "");
  const [password, setPassword] = useState(formData.wifi?.password || "");
  const [securityType, setSecurityType] = useState(
    formData.wifi?.securityType || "WPA",
  );
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.wifi?.qrCodeValue || "",
  );

  useEffect(() => {
    if (ssid && securityType) {
      const inputData = { ssid, password, securityType };
      const newQrCodeValue = generateQRCodeValue({ wifi: inputData });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("wifi", {
          ssid,
          password,
          securityType,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    ssid,
    password,
    securityType,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  const handleSsidChange = (e) => setSsid(e.target.value);
  const handlePasswordChange = (e) => setPassword(e.target.value);
  const handleSecurityTypeChange = (e) => setSecurityType(e.target.value);

  return (
    <form className="row">
      <div className="col s12 m6">
        <div className="input-field">
          <input
            type="text"
            id="ssid"
            placeholder={t("form.ssidPlaceholder")}
            value={ssid}
            onChange={handleSsidChange}
            required
          />
        </div>

        <div className="input-field">
          <input
            type="password"
            id="password"
            placeholder={t("form.passwordPlaceholder")}
            value={password}
            onChange={handlePasswordChange}
            required
          />
        </div>
      </div>

      <div className="col s12 m6">
        <div className="input-field">
          <select
            id="security-type"
            className="browser-default"
            value={securityType}
            onChange={handleSecurityTypeChange}
            required
          >
            <option value="" disabled>
              {t("form.chooseSecurityType")}
            </option>
            <option value="WPA">{t("form.wpa")}</option>
            <option value="WEP">{t("form.wep")}</option>
            <option value="None">{t("form.none")}</option>
          </select>
        </div>
      </div>
    </form>
  );
};
