import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * Component for creating a Bitcoin QR code form.
 *
 * @component
 * @returns {JSX.Element} The Bitcoin form for entering address and amount.
 */
export const BitcoinForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [bitcoinAddress, setBitcoinAddress] = useState(
    formData.bitcoin?.bitcoinAddress || "",
  );
  const [bitcoinAmount, setBitcoinAmount] = useState(
    formData.bitcoin?.bitcoinAmount || "",
  );
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.bitcoin?.qrCodeValue || "",
  );

  useEffect(() => {
    if (bitcoinAddress) {
      const inputData = {
        bitcoinAddress,
        bitcoinAmount,
      };
      const newQrCodeValue = generateQRCodeValue({ bitcoin: inputData });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("bitcoin", {
          bitcoinAddress,
          bitcoinAmount,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    bitcoinAddress,
    bitcoinAmount,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  return (
    <form className="row">
      <div className="col s12 m6">
        <div className="input-field">
          <input
            type="text"
            id="bitcoin-address"
            value={bitcoinAddress}
            onChange={(e) => setBitcoinAddress(e.target.value)}
            placeholder={t("form.bitcoinAddressPlaceholder")}
            required
          />
        </div>
      </div>

      <div className="col s12 m6">
        <div className="input-field">
          <input
            type="number"
            id="bitcoin-amount"
            value={bitcoinAmount}
            onChange={(e) => setBitcoinAmount(e.target.value)}
            placeholder={t("form.bitcoinAmountPlaceholder")}
          />
        </div>
      </div>
    </form>
  );
};
