import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * Component for creating an email QR code.
 *
 * @component
 * @returns {JSX.Element} The email form with fields for recipient, subject, and message.
 */
const EmailForm = () => {
  const { t } = useTranslation();
  const { updateFormData, setQrCodeVisible } = useContext(QRCodeContext);

  const [recipient, setRecipient] = useState("");
  const [subject, setSubject] = useState("");
  const [message, setMessage] = useState("");
  const [qrCodeValue, setQrCodeValue] = useState("");

  useEffect(() => {
    const shouldGenerateQRCode = recipient && subject;

    if (shouldGenerateQRCode) {
      const newQrCodeValue = generateQRCodeValue({
        email: { recipient, subject, message },
      });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("email", {
          recipient,
          subject,
          message,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    recipient,
    subject,
    message,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  const handleRecipientChange = (e) => setRecipient(e.target.value);
  const handleSubjectChange = (e) => setSubject(e.target.value);
  const handleMessageChange = (e) => setMessage(e.target.value);

  return (
    <form>
      <div className="row">
        <div className="input-field col s6">
          <input
            type="email"
            id="recipient"
            value={recipient}
            onChange={handleRecipientChange}
            required
          />
          <label htmlFor="recipient" className={recipient ? "active" : ""}>
            {t("form.recipientLabel", { defaultValue: "Recipient" })}
          </label>
        </div>
        <div className="input-field col s6">
          <input
            type="text"
            id="subject"
            value={subject}
            onChange={handleSubjectChange}
            required
          />
          <label htmlFor="subject" className={subject ? "active" : ""}>
            {t("form.subjectLabel", { defaultValue: "Subject" })}
          </label>
        </div>
        <div className="input-field col s12">
          <textarea
            id="message"
            className="materialize-textarea"
            value={message}
            onChange={handleMessageChange}
          ></textarea>
          <label htmlFor="message" className={message ? "active" : ""}>
            {t("form.messageLabel", { defaultValue: "Message" })}
          </label>
        </div>
      </div>
    </form>
  );
};

export default EmailForm;
