import React from "react";
import { useTranslation } from "react-i18next";
import config from "../config/config";

/**
 * Component for rendering the footer of the application.
 *
 * @component
 * @returns {JSX.Element} The footer section with links and descriptions.
 */
const Footer = () => {
  const { t } = useTranslation();

  return (
    <footer className="page-footer blue-grey darken-3">
      <div className="container">
        <div className="row">
          <div className="col l6 s12">
            <h5 className="white-text">
              {t(config.footerContent.generatorTitle)}
            </h5>
            <p className="grey-text text-lighten-4">
              {t(config.footerContent.generatorDescription)}
            </p>
          </div>
          <div className="col l4 offset-l2 s12">
            <h5 className="white-text">{t(config.footerContent.linksTitle)}</h5>
            <ul>
              {config.footerLinks.slice(0, 3).map((link) => (
                <li key={link.label}>
                  <a className="grey-text text-lighten-3" href={link.href}>
                    {t(link.label)}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
      <div className="footer-copyright">
        <div className="container">
          {config.poweredByText}
          <a
            className="grey-text text-lighten-4 right"
            href={config.footerLinks[3].href}
          >
            {t(config.footerLinks[3].label)}
          </a>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
