import React, { useEffect } from "react";
import M from "materialize-css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import config from "../config/config";

/**
 * Header component that displays the navigation menu and logo.
 * Initializes the Materialize Sidenav for mobile view.
 *
 * @component
 * @returns {JSX.Element} The header with a brand logo and navigation links.
 */
const Header = () => {
  useEffect(() => {
    M.Sidenav.init(document.querySelectorAll(".sidenav"));
  }, []);

  return (
    <header>
      <nav className="blue-grey darken-3">
        <div className="nav-wrapper container">
          <a
            href="/"
            className="brand-logo"
            style={{
              fontFamily: `${config.theme.fontFamily}`,
              display: "flex",
              alignItems: "center",
              gap: 10,
            }}
          >
            <img
              src={config.logoIcon} // Chemin vers l'image
              alt="Logo"
              style={{
                width: 30, // Utilise la propriété de taille pour la largeur de l'image
              }}
            />{" "}
            {config.appTitle}
          </a>

          <ul id="nav-mobile" className="right hide-on-med-and-down">
            {config.navLinks.map((link) => (
              <li key={link.label}>
                <a href={link.href}>{link.label}</a>
              </li>
            ))}
          </ul>
          <a href="/" data-target="mobile-menu" className="sidenav-trigger">
            <i className="material-icons">menu</i>
          </a>
        </div>
      </nav>
      <ul className="sidenav" id="mobile-menu">
        {config.navLinks.map((link) => (
          <li key={link.label}>
            <a
              href={link.href}
              style={{ fontFamily: `${config.theme.fontFamily}` }}
            >
              {link.label}
            </a>
          </li>
        ))}
      </ul>
    </header>
  );
};

export default Header;
