import React, { useContext } from "react";
import { QRCodeContext } from "../context/QRCodeContext";
import ColorInput from "./ColorInput";
import { getColorLabel } from "../utils/colorUtils";
import { Container } from "@mui/material";
import { useTranslation } from "react-i18next";
import config from "../config/config";

/**
 * Component for selecting QR code colors including background, foreground,
 * and gradient options. Provides options to upload a background image
 * and toggle transparency.
 *
 * @component
 * @returns {JSX.Element} The QR code color selection interface.
 */
const QRCodeColorSelector = () => {
  const {
    isTransparentBackground,
    setIsTransparentBackground,
    isGradient,
    setIsGradient,
    hasBackgroundImage,
    setHasBackgroundImage,
    setBackgroundImage,
  } = useContext(QRCodeContext);

  const { t } = useTranslation();

  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setBackgroundImage(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <Container>
      <div className="row">
        <div className="col s12 m6">
          <div className="input-field">
            <ColorInput
              colorType="background"
              labelText={t(getColorLabel("background"))}
            />
          </div>

          {!hasBackgroundImage && (
            <div className="switch left-align">
              <label>
                <input
                  type="checkbox"
                  checked={isTransparentBackground}
                  onChange={(e) => setIsTransparentBackground(e.target.checked)}
                  style={{ margin: 0, padding: 0, border: "1px solid black" }}
                />
                <span className="lever"></span>
                <span>{t("Transparent background")}</span>
              </label>
            </div>
          )}

          <div className="switch left-align">
            <label>
              <input
                type="checkbox"
                checked={hasBackgroundImage}
                onChange={(e) => {
                  setHasBackgroundImage(e.target.checked);
                }}
              />
              <span className="lever"></span>
              <span>{t("Background image")}</span>
            </label>
          </div>
          {hasBackgroundImage && (
            <div className="file-field input-field">
              <div className={`btn ${config.theme.primaryColor}`}>
                <span>{t("Upload Image")}</span>
                <input
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                />
              </div>
              <div className="file-path-wrapper">
                <input
                  className="file-path validate"
                  type="text"
                  placeholder={t("Upload an image")}
                />
              </div>
            </div>
          )}
        </div>

        <div className="col s12 m6">
          <div className="input-field">
            <ColorInput
              colorType="foreground"
              labelText={t(getColorLabel("foreground"))}
            />
          </div>

          <div className="switch left-align">
            <label>
              <input
                type="checkbox"
                checked={isGradient}
                onChange={(e) => setIsGradient(e.target.checked)}
              />
              <span className="lever"></span>
              <span>{t("Gradient")}</span>
            </label>
          </div>
          {isGradient && (
            <div>
              <div className="input-field">
                <ColorInput
                  colorType="gradient"
                  labelText={t(getColorLabel("gradient"))}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    </Container>
  );
};

export default QRCodeColorSelector;
