import React, { useContext, useRef } from "react";
import { QRCodeContext } from "../context/QRCodeContext";
import QRCodeTypeSelector from "../components/QRCodeTypeSelector";
import QRCodeColorSelector from "../components/QRCodeColorSelector";
import QRCodeSizeSelector from "../components/QRCodeSizeSelector";
import QRCodeLogoUploader from "../components/QRCodeLogoUploader";
import QRCodeDisplaySection from "../components/QRCodeDisplaySection";

/**
 * Component that handles the main QR code generation process.
 * Allows users to select QR code type, color, size, and upload a logo.
 * Also displays the generated QR code.
 *
 * @component
 * @returns {JSX.Element} The QR code generator section with all customization options.
 */
const QRCodeGenerator = () => {
  const { setQrType, qrSize, setQrSize } = useContext(QRCodeContext);
  const canvasRef = useRef(null);

  return (
    <div className="container">
      <div className="row">
        <div className="col s12 m8 card">
          <div className="section" style={{ marginTop: "1rem" }}>
            <QRCodeTypeSelector setQrType={setQrType} />
            <QRCodeColorSelector />
          </div>
          <QRCodeSizeSelector qrSize={qrSize} setQrSize={setQrSize} />
          <div className="section">
            <QRCodeLogoUploader />
          </div>
        </div>
        <QRCodeDisplaySection canvasRef={canvasRef} />
      </div>
    </div>
  );
};

export default QRCodeGenerator;
