import React, { useState, useEffect, useContext } from "react";
import "materialize-css/dist/css/materialize.min.css";
import "@fortawesome/fontawesome-free/css/all.min.css";

import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  useMap,
  useMapEvents,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import { LocationContext } from "../../context/LocationContext";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";

import markerIcon2x from "leaflet/dist/images/marker-icon-2x.png";
import markerIcon from "leaflet/dist/images/marker-icon.png";
import markerShadow from "leaflet/dist/images/marker-shadow.png";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

const defaultIcon = L.icon({
  iconUrl: markerIcon,
  iconRetinaUrl: markerIcon2x,
  shadowUrl: markerShadow,
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
});

L.Marker.prototype.options.icon = defaultIcon;

/**
 * Component that recenters the map on a new position.
 *
 * @param {Object} props
 * @param {Array<number>} props.position - The new position to center the map.
 * @returns {null}
 */
const RecenterMap = ({ position }) => {
  const map = useMap();

  useEffect(() => {
    if (position) {
      map.setView(position, 13);
    }
  }, [position, map]);

  return null;
};

/**
 * Component for creating a location QR code.
 *
 * @component
 * @returns {JSX.Element} The location form with a map and fields for latitude and longitude.
 */
const LocationForm = () => {
  const { locationData, setLocationData } = useContext(LocationContext);
  const { updateFormData, setQrCodeVisible } = useContext(QRCodeContext);
  const { latitude, longitude, address } = locationData;
  const [position, setPosition] = useState([latitude, longitude]);
  const [qrCodeValue, setQrCodeValue] = useState("");
  const { t } = useTranslation();

  useEffect(() => {
    setPosition([parseFloat(latitude), parseFloat(longitude)]);
  }, [latitude, longitude]);

  useEffect(() => {
    const shouldGenerateQRCode = latitude && longitude;

    if (shouldGenerateQRCode) {
      const newQrCodeValue = generateQRCodeValue({
        location: {
          latitude,
          longitude,
          address,
        },
      });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("location", {
          latitude,
          longitude,
          address,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    latitude,
    longitude,
    address,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  const searchAddress = async () => {
    if (address.trim() === "") return;

    const response = await fetch(
      `https://nominatim.openstreetmap.org/search?q=${address}&format=json&limit=1`,
    );
    const data = await response.json();

    if (data.length > 0) {
      const result = data[0];
      const newLat = result.lat;
      const newLng = result.lon;

      setLocationData({
        ...locationData,
        latitude: newLat,
        longitude: newLng,
      });

      const newQrCodeValue = generateQRCodeValue("location", {
        latitude: newLat,
        longitude: newLng,
        address: result.display_name,
      });
      setQrCodeValue(newQrCodeValue);
      updateFormData("location", {
        latitude: newLat,
        longitude: newLng,
        address: result.display_name,
        qrCodeValue: newQrCodeValue,
      });
      setQrCodeVisible(true);
    } else {
      alert("Adresse non trouvée !");
    }
  };

  const LocationMarker = () => {
    useMapEvents({
      click(e) {
        const newLat = e.latlng.lat;
        const newLng = e.latlng.lng;

        setPosition([newLat, newLng]);
        setLocationData({
          ...locationData,
          latitude: newLat,
          longitude: newLng,
        });

        const newQrCodeValue = generateQRCodeValue("location", {
          latitude: newLat,
          longitude: newLng,
          address,
        });
        setQrCodeValue(newQrCodeValue);
        updateFormData("location", {
          latitude: newLat,
          longitude: newLng,
          address,
          qrCodeValue: newQrCodeValue,
        });
        setQrCodeVisible(true);
      },
    });

    return position === null ? null : (
      <Marker position={position}>
        <Popup>
          Latitude: {latitude}, Longitude: {longitude}
        </Popup>
      </Marker>
    );
  };

  return (
    <form>
      <div className="row">
        <div className="input-field col s12">
          <div className="row">
            <div className="col s10">
              <input
                type="text"
                id="location-search"
                value={address}
                onChange={(e) =>
                  setLocationData({ ...locationData, address: e.target.value })
                }
                placeholder={t("form.addressPlaceholder")}
                required
              />
            </div>
            <div className="col s1">
              <button
                type="button"
                className="btn-floating btn-small waves-effect waves-light green"
                onClick={searchAddress}
                style={{ marginTop: "25px" }}
              >
                <i className="fas fa-search"></i>
              </button>
            </div>
          </div>
        </div>

        <div className="input-field col s6">
          <input
            type="text"
            id="latitude"
            value={latitude}
            onChange={(e) =>
              setLocationData({ ...locationData, latitude: e.target.value })
            }
            required
          />
          <label htmlFor="latitude" className="active">
            Latitude
          </label>
        </div>

        <div className="input-field col s6">
          <input
            type="text"
            id="longitude"
            value={longitude}
            onChange={(e) =>
              setLocationData({ ...locationData, longitude: e.target.value })
            }
            required
          />
          <label htmlFor="longitude" className="active">
            Longitude
          </label>
        </div>

        <div className="col s12">
          <MapContainer
            center={position}
            zoom={13}
            style={{ height: "300px", marginBottom: "20px" }}
          >
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            />
            <LocationMarker />
            <RecenterMap position={position} />
          </MapContainer>
        </div>
      </div>
    </form>
  );
};

RecenterMap.propTypes = {
  position: PropTypes.arrayOf(PropTypes.number).isRequired,
};

export default LocationForm;
