import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * PayPalForm component for handling PayPal payment details.
 *
 * @component
 * @returns {JSX.Element} The rendered PayPal form.
 */
const PayPalForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [paymentType, setPaymentType] = useState(
    formData.paypal?.paymentType || "",
  );
  const [paypalEmail, setPaypalEmail] = useState(
    formData.paypal?.paypalEmail || "",
  );
  const [itemName, setItemName] = useState(formData.paypal?.itemName || "");
  const [itemId, setItemId] = useState(formData.paypal?.itemId || "");
  const [itemPrice, setItemPrice] = useState(formData.paypal?.itemPrice || "");
  const [itemCurrency, setItemCurrency] = useState(
    formData.paypal?.itemCurrency || "",
  );
  const [shipping, setShipping] = useState(formData.paypal?.shipping || "");
  const [shippingCurrency, setShippingCurrency] = useState(
    formData.paypal?.shippingCurrency || "",
  );
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.paypal?.qrCodeValue || "",
  );

  useEffect(() => {
    if (paypalEmail && itemName && itemId && itemPrice && itemCurrency) {
      const inputData = {
        paypalEmail,
        itemName,
        itemId,
        itemPrice,
        itemCurrency,
        shipping,
        shippingCurrency,
        paymentType,
      };
      const newQrCodeValue = generateQRCodeValue({ paypal: inputData });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("paypal", {
          paymentType,
          paypalEmail,
          itemName,
          itemId,
          itemPrice,
          itemCurrency,
          shipping,
          shippingCurrency,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    paypalEmail,
    itemName,
    itemId,
    itemPrice,
    itemCurrency,
    shipping,
    shippingCurrency,
    paymentType,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  return (
    <form className="row">
      <div className="input-field col s6">
        <select
          id="payment-type"
          className="browser-default"
          value={paymentType}
          onChange={(e) => setPaymentType(e.target.value)}
          required
        >
          <option value="" disabled>
            {t("form.paymentTypeLabel")}
          </option>
          <option value="buy-now">{t("form.buyNow")}</option>
          <option value="add-to-cart">{t("form.addToCart")}</option>
          <option value="donations">{t("form.donations")}</option>
        </select>
      </div>

      <div className="input-field col s6">
        <input
          type="email"
          id="paypal-email"
          value={paypalEmail}
          onChange={(e) => setPaypalEmail(e.target.value)}
          required
          placeholder={t("form.paypalEmailPlaceholder")}
        />
      </div>

      <div className="input-field col s6">
        <input
          type="text"
          id="item-name"
          value={itemName}
          onChange={(e) => setItemName(e.target.value)}
          required
        />
        <label htmlFor="item-name" className="active">
          {t("form.itemNameLabel")}
        </label>
      </div>

      <div className="input-field col s6">
        <input
          type="text"
          id="item-id"
          value={itemId}
          onChange={(e) => setItemId(e.target.value)}
          required
        />
        <label htmlFor="item-id" className="active">
          {t("form.itemIdLabel")}
        </label>
      </div>

      <div className="input-field col s6">
        <div className="row">
          <div className="col s8">
            <input
              type="number"
              id="item-price"
              value={itemPrice}
              onChange={(e) => setItemPrice(e.target.value)}
              required
              placeholder={t("form.itemPricePlaceholder")}
            />
          </div>
          <div className="col s4">
            <select
              id="item-currency"
              className="browser-default"
              value={itemCurrency}
              onChange={(e) => setItemCurrency(e.target.value)}
              required
            >
              <option value="" disabled>
                {t("form.currencyLabel")}
              </option>
              <option value="USD">USD</option>
              <option value="EUR">EUR</option>
              <option value="GBP">GBP</option>
            </select>
          </div>
        </div>
      </div>

      <div className="input-field col s12">
        <div className="row">
          <div className="col s8">
            <input
              type="text"
              id="shipping"
              value={shipping}
              onChange={(e) => setShipping(e.target.value)}
              required
              placeholder={t("form.shippingPlaceholder")}
            />
          </div>
          <div className="col s4">
            <select
              id="shipping-currency"
              className="browser-default"
              value={shippingCurrency}
              onChange={(e) => setShippingCurrency(e.target.value)}
              required
            >
              <option value="" disabled>
                {t("form.currencyLabel")}
              </option>
              <option value="USD">USD</option>
              <option value="EUR">EUR</option>
              <option value="GBP">GBP</option>
            </select>
          </div>
        </div>
      </div>
    </form>
  );
};

export default PayPalForm;
