import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * Component for creating an event QR code.
 *
 * @component
 * @returns {JSX.Element} The event form with fields for event name, date, time, and location.
 */
export const EventForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [eventName, setEventName] = useState(formData.event?.eventName || "");
  const [eventDate, setEventDate] = useState(formData.event?.eventDate || "");
  const [eventTime, setEventTime] = useState(formData.event?.eventTime || "");
  const [eventLocation, setEventLocation] = useState(
    formData.event?.eventLocation || "",
  );
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.event?.qrCodeValue || "",
  );

  useEffect(() => {
    if (eventName && eventDate && eventTime) {
      const combinedDateTime = new Date(`${eventDate}T${eventTime}`);

      if (!isNaN(combinedDateTime.getTime())) {
        const inputData = { eventName, eventDate, eventTime, eventLocation };
        const newQrCodeValue = generateQRCodeValue({ event: inputData });

        if (newQrCodeValue !== qrCodeValue) {
          setQrCodeValue(newQrCodeValue);
          updateFormData("event", {
            eventName,
            eventDate,
            eventTime,
            eventLocation,
            qrCodeValue: newQrCodeValue,
          });
        }
        setQrCodeVisible(true);
      } else {
        setQrCodeVisible(false);
      }
    } else {
      setQrCodeVisible(false);
    }
  }, [eventName, eventDate, eventTime, eventLocation]);

  const handleEventNameChange = (e) => setEventName(e.target.value);
  const handleEventDateChange = (e) => setEventDate(e.target.value);
  const handleEventTimeChange = (e) => setEventTime(e.target.value);
  const handleEventLocationChange = (e) => setEventLocation(e.target.value);

  return (
    <form className="row">
      <div className="input-field col s12">
        <input
          type="text"
          id="event-name"
          placeholder={t("form.eventNamePlaceholder")}
          value={eventName}
          onChange={handleEventNameChange}
          required
        />
      </div>

      <div className="input-field col s6">
        <input
          type="date"
          id="event-date"
          value={eventDate}
          onChange={handleEventDateChange}
          required
        />
        <label htmlFor="event-date" className="active">
          {t("form.eventDateLabel")}
        </label>
      </div>

      <div className="input-field col s6">
        <input
          type="time"
          id="event-time"
          value={eventTime}
          onChange={handleEventTimeChange}
          required
        />
        <label htmlFor="event-time" className="active">
          {t("form.eventTimeLabel")}
        </label>
      </div>

      <div className="input-field col s12">
        <input
          type="text"
          id="event-location"
          placeholder={t("form.eventLocationPlaceholder")}
          value={eventLocation}
          onChange={handleEventLocationChange}
        />
        <label htmlFor="event-location" className="active">
          {t("form.eventLocationLabel")}
        </label>
      </div>
    </form>
  );
};
