import React, { useContext, useEffect, useRef } from "react";
import { QRCodeCanvas } from "qrcode.react";
import { determineBackgroundColor } from "../utils/qrCodeUtils";
import { QRCodeContext } from "../context/QRCodeContext";
import { applyGradient, drawBackgroundImage } from "../utils/canvasUtils";
import QRCodeActionButtons from "./QRCodeActionButtons";
import PropTypes from "prop-types";

/**
 * Component for displaying the generated QR code with customizable options
 * including background gradient, color, and optional logo. It utilizes
 * a canvas to manage the background rendering effects.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {Object} props.canvasRef - Reference to the canvas element.
 * @returns {JSX.Element|null} The QR code display with action buttons or null.
 */
const QRCodeDisplay = ({ canvasRef }) => {
  const {
    qrCodeVisible,
    qrType,
    qrSize,
    qrColor,
    logoImage,
    qrBackgroundColor,
    isGradient,
    gradientColor,
    isTransparentBackground,
    hasBackgroundImage,
    backgroundImage,
    formData,
  } = useContext(QRCodeContext);

  const gradientCanvasRef = useRef(null);

  useEffect(() => {
    if (gradientCanvasRef.current) {
      const ctx = gradientCanvasRef.current.getContext("2d");
      const { width, height } = gradientCanvasRef.current;
      if (isGradient) {
        applyGradient(ctx, qrBackgroundColor, gradientColor, width, height);
      } else if (hasBackgroundImage && backgroundImage) {
        drawBackgroundImage(ctx, backgroundImage, width, height).catch(
          (error) => {
            console.error("Error loading background image:", error);
          },
        );
      }
    }
  }, [
    isGradient,
    gradientColor,
    qrSize,
    qrBackgroundColor,
    hasBackgroundImage,
    backgroundImage,
  ]);

  const shouldShowCanvas =
    isGradient || hasBackgroundImage || isTransparentBackground;

  const maxQRSize = qrSize < 200 ? qrSize : 200;

  return qrCodeVisible ? (
    <div className="card">
      <div className="custom-card-image">
        {shouldShowCanvas && (
          <canvas
            ref={gradientCanvasRef}
            width={maxQRSize}
            height={maxQRSize}
            style={{
              position: "absolute",
              zIndex: 1,
            }}
          />
        )}

        <div
          id="qr-code"
          ref={canvasRef}
          style={{
            position: "relative",
            width: maxQRSize,
            height: maxQRSize,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 2,
          }}
        >
          <QRCodeCanvas
            value={formData[qrType]?.qrCodeValue}
            size={qrSize}
            bgColor={determineBackgroundColor(
              isGradient,
              qrBackgroundColor,
              isTransparentBackground,
              hasBackgroundImage,
              backgroundImage,
            )}
            fgColor={qrColor}
            level="H"
            imageSettings={
              logoImage
                ? {
                    src: logoImage,
                    x: null,
                    y: null,
                    height: qrSize / 5,
                    width: qrSize / 5,
                    excavate: true,
                  }
                : null
            }
            style={{
              maxWidth: 200,
              maxHeight: 200,
            }}
          />
        </div>
        <QRCodeActionButtons canvasRef={canvasRef} />
      </div>
    </div>
  ) : null;
};

// PropTypes validation
QRCodeDisplay.propTypes = {
  canvasRef: PropTypes.object.isRequired,
};

export default QRCodeDisplay;
