import React from "react";
import { QRCodeCanvas } from "qrcode.react";

/**
 * Component that displays a placeholder QR code when no QR code is generated.
 *
 * @component
 * @returns {JSX.Element} The placeholder QR code.
 */
const QRCodePlaceholder = () => {
  return (
    <div className="card">
      <div className="custom-card-image">
        <QRCodeCanvas
          value="QR Code en attente"
          size={200}
          bgColor="#ffffff"
          fgColor="#000000"
          level="H"
        />
      </div>
    </div>
  );
};

export default QRCodePlaceholder;
