import React from "react";
import { BitcoinForm } from "../components/forms/BitcoinForm";
import EmailForm from "../components/forms/EmailForm";
import { EventForm } from "../components/forms/EventForm";
import LocationForm from "../components/forms/LocationForm";
import PayPalForm from "../components/forms/PayPalForm";
import PhoneForm from "../components/forms/PhoneForm";
import { SkypeForm } from "../components/forms/SkypeForm";
import SmsForm from "../components/forms/SmsForm";
import { TextForm } from "../components/forms/TextForm";
import { UrlForm } from "../components/forms/UrlForm";
import { VCardForm } from "../components/forms/VCardForm";
import WhatsAppForm from "../components/forms/WhatsAppForm";
import { WifiForm } from "../components/forms/WifiForm";
import { ZoomForm } from "../components/forms/ZoomForm";

/**
 * Renders the form component corresponding to the specified QR code type.
 *
 * @param {string} qrType - The QR code type for which to render the form.
 * @returns {JSX.Element|null} - The form component to render or null if no match.
 */
export const renderForm = (qrType) => {
  switch (qrType) {
    case "text":
      return <TextForm />;
    case "url":
      return <UrlForm />;
    case "email":
      return <EmailForm />;
    case "tel":
      return <PhoneForm />;
    case "location":
      return <LocationForm />;
    case "sms":
      return <SmsForm />;
    case "whatsapp":
      return <WhatsAppForm />;
    case "skype":
      return <SkypeForm />;
    case "zoom":
      return <ZoomForm />;
    case "wifi":
      return <WifiForm />;
    case "vcard":
      return <VCardForm />;
    case "event":
      return <EventForm />;
    case "paypal":
      return <PayPalForm />;
    case "bitcoin":
      return <BitcoinForm />;
    default:
      return null;
  }
};
