import PropTypes from "prop-types";
import React, { createContext, useState } from "react";
import config from "../config/config";

/**
 * Creates the QR code context.
 */
export const QRCodeContext = createContext();

/**
 * QRCodeProvider component to provide QR code data and functions to its children.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {React.ReactNode} props.children - The children components that will have access to the QR code context.
 * @returns {JSX.Element} The provider wrapping its children.
 */
export const QRCodeProvider = ({ children }) => {
  // Shared states for the entire form
  const [inputText, setInputText] = useState("");
  const [qrType, setQrType] = useState("text");
  const [qrColor, setQrColor] = useState(config.defaultQRCodeColor);
  const [qrSize, setQrSize] = useState(config.defaultQRCodeSize);
  const [logoImage, setLogoImage] = useState(null);
  const [qrCodeVisible, setQrCodeVisible] = useState(false);
  const [qrBackgroundColor, setQrBackgroundColor] = useState(
    config.defaultQRCodeBackgroundColor,
  );
  const [isTransparentBackground, setIsTransparentBackground] = useState(false);
  const [isGradient, setIsGradient] = useState(false);
  const [hasBackgroundImage, setHasBackgroundImage] = useState(false);
  const [gradientColor, setGradientColor] = useState(
    config.defaultQRCodegradientColor,
  );
  const [backgroundImage, setBackgroundImage] = useState(null);
  const [formData, setFormData] = useState({});

  /**
   * Updates the form data in the context state.
   *
   * @param {string} type - The type of data to update.
   * @param {Object} newData - The new data to merge with the existing data.
   */
  const updateFormData = (type, newData) => {
    setFormData((prevFormData) => ({
      ...prevFormData,
      [type]: { ...prevFormData[type], ...newData },
    }));
  };

  // All states and setters in an object to be shared via context
  const qrCodeStates = {
    inputText,
    setInputText,
    qrType,
    setQrType,
    qrColor,
    setQrColor,
    gradientColor,
    setGradientColor,
    qrSize,
    setQrSize,
    logoImage,
    setLogoImage,
    backgroundImage,
    setBackgroundImage,
    qrCodeVisible,
    setQrCodeVisible,
    qrBackgroundColor,
    setQrBackgroundColor,
    isTransparentBackground,
    setIsTransparentBackground,
    isGradient,
    setIsGradient,
    hasBackgroundImage,
    setHasBackgroundImage,
    formData,
    updateFormData,
  };

  return (
    <QRCodeContext.Provider value={qrCodeStates}>
      {children}
    </QRCodeContext.Provider>
  );
};

QRCodeProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
