/**
 * Generates the QR code value based on the provided form data.
 *
 * @param {Object} formData - The data used to generate the QR code value.
 * @returns {string} - The generated QR code value as a string.
 */
export const generateQRCodeValue = (formData) => {
  if (!formData || Object.keys(formData).length === 0) return "";

  let qrCodeValue = "";

  if (formData.tel) {
    const { countryCode, phoneNumber } = formData.tel;
    if (phoneNumber) qrCodeValue = `tel:${countryCode}${phoneNumber}`;
  } else if (formData.whatsapp) {
    const { countryCode, phoneNumber, message } = formData.whatsapp;
    if (phoneNumber) {
      qrCodeValue = `https://wa.me/${countryCode}${phoneNumber}?text=${encodeURIComponent(message || "")}`;
    }
  } else if (formData.sms) {
    const { countryCode, phoneNumber, message } = formData.sms;
    if (phoneNumber) {
      qrCodeValue = `sms:${countryCode}${phoneNumber}?body=${encodeURIComponent(message || "")}`;
    }
  } else if (formData.email) {
    const { recipient, subject, text } = formData.email;
    if (recipient) {
      qrCodeValue = `mailto:${recipient}?subject=${encodeURIComponent(subject || "")}&body=${encodeURIComponent(text || "")}`;
    }
  } else if (formData.url) {
    const { link } = formData.url;
    if (link) qrCodeValue = encodeURIComponent(link);
  } else if (formData.location) {
    const { latitude, longitude, address } = formData.location;
    if (latitude && longitude) {
      qrCodeValue = address
        ? `geo:${latitude},${longitude}?q=${encodeURIComponent(address)}`
        : `geo:${latitude},${longitude}`;
    }
  } else if (formData.wifi) {
    const { ssid, password, security } = formData.wifi;
    if (ssid) {
      qrCodeValue = `WIFI:S:${ssid};T:${security || "WPA"};P:${password || ""};H:;`;
    }
  } else if (formData.text) {
    const { text } = formData.text;
    if (text) qrCodeValue = `TEXT:${text}`;
  } else if (formData.zoom) {
    const { zoomURL } = formData.zoom;
    if (zoomURL)
      qrCodeValue = `zoommtg://zoom.us/join?action=join&confno=${zoomURL}`;
  } else if (formData.paypal) {
    const {
      paymentType,
      paypalEmail,
      itemName,
      itemId,
      itemPrice,
      itemCurrency,
      shipping,
    } = formData.paypal;
    if (paypalEmail) {
      if (paymentType === "paypal.me")
        qrCodeValue = `https://paypal.me/${paypalEmail}`;
      else if (paymentType === "donation")
        qrCodeValue = `https://www.paypal.com/donate?business=${paypalEmail}`;
      else if (paymentType === "checkout")
        qrCodeValue = `https://www.paypal.com/cgi-bin/webscr?cmd=_xclick&business=${paypalEmail}`;

      if (itemName) qrCodeValue += `&item_name=${encodeURIComponent(itemName)}`;
      if (itemId) qrCodeValue += `&item_id=${encodeURIComponent(itemId)}`;
      if (itemPrice) qrCodeValue += `&amount=${itemPrice}`;
      if (itemCurrency) qrCodeValue += `&currency_code=${itemCurrency}`;
      if (shipping) qrCodeValue += `&shipping=${shipping}`;
    }
  } else if (formData.bitcoin) {
    const { bitcoinAddress, bitcoinAmount } = formData.bitcoin;
    if (bitcoinAddress) {
      qrCodeValue = `bitcoin:${bitcoinAddress}`;
      if (bitcoinAmount) qrCodeValue += `?amount=${bitcoinAmount}`;
    }
  } else if (formData.vcard) {
    const { name, email, phone } = formData.vcard;
    if (name && email && phone) {
      qrCodeValue = `BEGIN:VCARD\nVERSION:3.0\nFN:${name}\nEMAIL:${email}\nTEL:${phone}\nEND:VCARD`;
    }
  } else if (formData.event) {
    const { eventName, eventDate, eventTime, eventLocation } = formData.event;
    if (eventName && eventDate && eventTime) {
      const combinedDateTime = new Date(`${eventDate}T${eventTime}`);
      const startDate =
        combinedDateTime.toISOString().replace(/[-:]/g, "").split(".")[0] + "Z";
      qrCodeValue = `
BEGIN:VEVENT
SUMMARY:${eventName}
DTSTART:${startDate}
LOCATION:${eventLocation}
END:VEVENT
END:VCALENDAR
    `.trim();
    }
  } else if (formData.skype) {
    const { skypeId } = formData.skype;
    if (skypeId) qrCodeValue = `skype:${skypeId}?call`;
  }

  return qrCodeValue;
};

/**
 * Downloads the QR code image from a canvas element, applying background options.
 *
 * @param {React.RefObject} canvasRef - Reference to the QR code canvas element.
 * @param {string} gradientColor - The gradient color for the QR code background.
 * @param {string} qrBackgroundColor - The QR code background color.
 * @param {boolean} isGradient - Flag for using a gradient background.
 * @param {boolean} hasBackgroundImage - Flag for using a background image.
 * @param {string} backgroundImage - URL of the background image to apply.
 */
export const downloadQRCode = (
  canvasRef,
  gradientColor,
  qrBackgroundColor,
  isGradient,
  hasBackgroundImage,
  backgroundImage,
) => {
  const canvas = canvasRef.current?.querySelector("canvas");
  if (!canvas) return;

  const size = canvas.width;
  const finalCanvas = document.createElement("canvas");
  const ctx = finalCanvas.getContext("2d");
  finalCanvas.width = size;
  finalCanvas.height = size;

  if (isGradient) {
    const gradient = ctx.createLinearGradient(0, 0, size, size);
    gradient.addColorStop(0, qrBackgroundColor);
    gradient.addColorStop(1, gradientColor);
    ctx.fillStyle = gradient;
    ctx.fillRect(0, 0, size, size);
  }

  if (hasBackgroundImage && backgroundImage) {
    const img = new Image();
    img.src = backgroundImage;
    img.onload = () => {
      ctx.drawImage(img, 0, 0, size, size);
      ctx.drawImage(canvas, 0, 0);
      downloadFinalImage(finalCanvas);
    };
  } else {
    ctx.drawImage(canvas, 0, 0);
    downloadFinalImage(finalCanvas);
  }
};

/**
 * Initiates the download of the provided canvas as an image file.
 *
 * @param {HTMLCanvasElement} canvas - The canvas element to download as an image.
 */
const downloadFinalImage = (canvas) => {
  const link = document.createElement("a");
  link.download = "qr-code.png";
  link.href = canvas.toDataURL();
  link.click();
};

/**
 * Determines the appropriate background color for the QR code.
 *
 * @param {boolean} isGradient - Indicates if a gradient background is used.
 * @param {string} qrBackgroundColor - The background color for the QR code.
 * @param {boolean} isTransparentBackground - Flag for transparent background.
 * @param {boolean} hasBackgroundImage - Flag for a background image.
 * @param {string} backgroundImage - URL of the background image if applicable.
 * @returns {string} - The determined background color.
 */
export const determineBackgroundColor = (
  isGradient,
  qrBackgroundColor,
  isTransparentBackground,
  hasBackgroundImage,
  backgroundImage,
) => {
  if (isTransparentBackground) return "transparent";
  if (hasBackgroundImage)
    return backgroundImage ? "transparent" : qrBackgroundColor;
  if (isGradient) return "transparent";
  return qrBackgroundColor;
};
