// LocationContext.js
import PropTypes from "prop-types";
import React, { createContext, useState } from "react";

/**
 * Creates the location context.
 */
export const LocationContext = createContext();

/**
 * LocationProvider component to provide location data to its children.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {React.ReactNode} props.children - The children components that will have access to the location context.
 * @returns {JSX.Element} The provider wrapping its children.
 */
export const LocationProvider = ({ children }) => {
  const [locationData, setLocationData] = useState({
    latitude: 51.505,
    longitude: -0.09,
    address: "",
  });

  return (
    <LocationContext.Provider value={{ locationData, setLocationData }}>
      {children}
    </LocationContext.Provider>
  );
};

LocationProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
