import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * VCardForm component for handling VCard input fields and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered VCard input form.
 */
export const VCardForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [name, setName] = useState(formData.vcard?.name || "");
  const [phone, setPhone] = useState(formData.vcard?.phone || "");
  const [company, setCompany] = useState(formData.vcard?.company || "");
  const [email, setEmail] = useState(formData.vcard?.email || "");
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.vcard?.qrCodeValue || "",
  );

  useEffect(() => {
    if (name && phone && email) {
      const inputData = { name, email, phone, company };
      const newQrCodeValue = generateQRCodeValue({ vcard: inputData });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("vcard", {
          name,
          phone,
          company,
          email,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [
    name,
    phone,
    company,
    email,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  const handleNameChange = (e) => setName(e.target.value);
  const handlePhoneChange = (e) => setPhone(e.target.value);
  const handleCompanyChange = (e) => setCompany(e.target.value);
  const handleEmailChange = (e) => setEmail(e.target.value);

  return (
    <form className="row">
      <div className="col s12 m6">
        <div className="input-field">
          <input
            type="text"
            id="name"
            placeholder={t("form.namePlaceholder")}
            value={name}
            onChange={handleNameChange}
            required
          />
        </div>

        <div className="input-field">
          <input
            type="tel"
            id="phone"
            placeholder={t("form.phonePlaceholder")}
            value={phone}
            onChange={handlePhoneChange}
            required
          />
        </div>
      </div>

      <div className="col s12 m6">
        <div className="input-field">
          <input
            type="text"
            id="company"
            placeholder={t("form.companyPlaceholder")}
            value={company}
            onChange={handleCompanyChange}
          />
        </div>

        <div className="input-field">
          <input
            type="email"
            id="email"
            placeholder={t("form.emailPlaceholder")}
            value={email}
            onChange={handleEmailChange}
            required
          />
        </div>
      </div>
    </form>
  );
};
