import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { countries } from "../../data/countries";
import { useTranslation } from "react-i18next";

/**
 * SmsForm component for handling SMS input and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered SMS form.
 */
const SmsForm = () => {
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);
  const { t } = useTranslation();

  const [countryCode, setCountryCode] = useState(
    formData.sms?.countryCode || "",
  );
  const [phoneNumber, setPhoneNumber] = useState(
    formData.sms?.phoneNumber || "",
  );
  const [message, setMessage] = useState(formData.sms?.message || "");
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.sms?.qrCodeValue || "",
  );

  useEffect(() => {
    if (!formData.sms?.qrCodeValue) {
      setQrCodeVisible(false);
    }
  }, [formData, setQrCodeVisible]);

  useEffect(() => {
    const shouldGenerateQRCode = phoneNumber.length > 5 && countryCode;

    if (shouldGenerateQRCode) {
      const newQrCodeValue = generateQRCodeValue({
        sms: { countryCode, phoneNumber, message },
      });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("sms", {
          countryCode,
          phoneNumber,
          message,
          qrCodeValue: newQrCodeValue,
        });
      }
      setQrCodeVisible(true);
    }
  }, [
    countryCode,
    phoneNumber,
    message,
    qrCodeValue,
    updateFormData,
    setQrCodeVisible,
  ]);

  const handleSmsChange = (e) => {
    setPhoneNumber(e.target.value);
  };

  const handleCountryChange = (e) => {
    setCountryCode(e.target.value);
  };

  const handleMessageChange = (e) => {
    setMessage(e.target.value);
  };

  return (
    <form>
      <div className="row">
        <div className="input-field col s3">
          <select
            value={countryCode}
            onChange={handleCountryChange}
            className="browser-default"
          >
            <option value="" disabled>
              {t("form.chooseCountry")}
            </option>
            {countries.map((country) => (
              <option key={country.name} value={country.code}>
                {country.name} ({country.code})
              </option>
            ))}
          </select>
        </div>

        <div className="input-field col s9">
          <input
            type="number"
            id="sms-phone"
            placeholder={t("form.phonePlaceholder")}
            value={phoneNumber}
            onChange={handleSmsChange}
            required
          />
        </div>

        <div className="input-field col s12">
          <textarea
            id="sms-message"
            className="materialize-textarea active"
            value={message}
            onChange={handleMessageChange}
          ></textarea>
          <label htmlFor="sms-message" className="active">
            {t("form.messageLabel")}
          </label>
        </div>
      </div>
    </form>
  );
};

export default SmsForm;
