import React, { useContext } from "react";
import { QRCodeContext } from "../context/QRCodeContext";
import { downloadQRCode } from "../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";
import PropTypes from "prop-types";

/**
 * Component that renders action buttons for downloading the QR code.
 * Uses context to access QR code properties and allows users to download
 * the QR code with the selected styles and settings.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {string} props.canvasRef - Reference to the canvas element for QR code rendering.
 * @returns {JSX.Element} The action buttons for downloading the QR code.
 */
const QRCodeActionButtons = ({ canvasRef }) => {
  const { t } = useTranslation();
  const {
    gradientColor,
    qrBackgroundColor,
    isGradient,
    hasBackgroundImage,
    backgroundImage,
  } = useContext(QRCodeContext);

  /**
   * Handles the download of the QR code by calling the downloadQRCode utility function.
   */
  const handleDownloadQRCode = () => {
    downloadQRCode(
      canvasRef,
      gradientColor,
      qrBackgroundColor,
      isGradient,
      hasBackgroundImage,
      backgroundImage,
    );
  };

  return (
    <div className="center-align">
      <div className="center-align" style={{ marginTop: "20px" }}>
        <button
          className="btn green waves-effect waves-light"
          onClick={handleDownloadQRCode}
          style={{
            fontWeight: "500",
            borderRadius: "5px",
          }}
        >
          {t("button.downloadQRCode")}
        </button>
      </div>
    </div>
  );
};

QRCodeActionButtons.propTypes = {
  canvasRef: PropTypes.string.isRequired,
};

export default QRCodeActionButtons;
