import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import translationEN from "./locales/en/translation.json";
import translationFR from "./locales/fr/translation.json";

/**
 * i18n configuration for setting up internationalization in the application.
 * Initializes the translations for English and French.
 *
 * @module i18n
 */
i18n.use(initReactI18next).init({
  /**
   * Translation resources, organized by language.
   * @type {Object}
   */
  resources: {
    en: {
      translation: translationEN,
    },
    fr: {
      translation: translationFR,
    },
  },

  /**
   * Default language for the application.
   * @type {string}
   */
  lng: "en",

  /**
   * Fallback language if a translation is missing.
   * @type {string}
   */
  fallbackLng: "en",

  interpolation: {
    /**
     * Disables escaping of values, as React already handles this.
     * @type {boolean}
     */
    escapeValue: false,
  },
});

export default i18n;
