import React, { useState, useContext } from "react";
import { Button, Grid2 as Grid, Container } from "@mui/material";
import { QRCodeContext } from "../context/QRCodeContext";
import { renderForm } from "../utils/formUtils";
import { useTranslation } from "react-i18next";
import { buttons as rawButtons } from "../data/buttons";
import config from "../config/config";

/**
 * Component for selecting QR code types and displaying the corresponding form.
 * Translates button labels, manages active button state, and shows the form for the selected QR code type.
 *
 * @component
 * @returns {JSX.Element} The QR code type selector section with buttons and dynamic form rendering.
 */
const QRCodeTypeSelector = () => {
  const { qrType, setQrType } = useContext(QRCodeContext);
  const [activeButton, setActiveButton] = useState(qrType);
  const [formVisible, setFormVisible] = useState(true);
  const { t } = useTranslation();

  const buttons = rawButtons.map((button) => ({
    ...button,
    label: t(button.label),
  }));

  /**
   * Handles button clicks to update QR code type, activate the selected button,
   * and toggle form visibility with a smooth transition.
   *
   * @param {string} value - The selected QR code type value.
   */
  const handleClick = (value) => {
    setFormVisible(false);
    setTimeout(() => {
      setQrType(value);
      setActiveButton(value);
      setFormVisible(true);
    }, 300);
  };

  return (
    <Container maxWidth="md">
      <Grid container spacing={2}>
        {buttons.map((button, index) => (
          <Grid size={4} key={index}>
            <Button
              fullWidth
              className={`waves-effect waves-light btn ${activeButton === button.value ? `${config.theme.primaryColor} darken-2` : "white lighten-1"}`}
              onClick={() => handleClick(button.value)}
              style={{
                color:
                  activeButton === button.value
                    ? "white"
                    : `${config.theme.secondaryColor}`,
                textTransform: "none",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                padding: "12px 20px",
                fontSize: "16px",
                fontWeight: "500",
                boxShadow: "0px 2px 6px rgba(0, 0, 0, 0.3)",
                transition: "background-color 0.3s, color 0.3s",
                width: "100%",
              }}
            >
              {button.icon}
              <span
                style={{
                  marginLeft: 8,
                  fontFamily: `${config.theme.fontFamily}`,
                }}
              >
                {button.label}
              </span>
            </Button>
          </Grid>
        ))}
      </Grid>
      <div className={`expand-down ${formVisible ? "show" : ""}`}>
        <div className={formVisible ? "slide-in" : ""}>
          {renderForm(qrType)}
        </div>
      </div>
    </Container>
  );
};

export default QRCodeTypeSelector;
