import { Container } from "@mui/material";
import React from "react";
import PropTypes from "prop-types";
import { useTranslation } from "react-i18next";

/**
 * Component for selecting the size of the QR code.
 *
 * @component
 * @param {Object} props - Component props.
 * @param {number} props.qrSize - Current size of the QR code.
 * @param {Function} props.setQrSize - Function to update the size of the QR code.
 * @returns {JSX.Element} The QR code size selection dropdown.
 */
const QRCodeSizeSelector = ({ qrSize, setQrSize }) => {
  const { t } = useTranslation();

  return (
    <Container>
      <div className="input-field">
        <label
          htmlFor="qr-size"
          style={{ fontSize: "1.2rem", color: "#26a69a" }}
        ></label>
        <select
          id="qr-size"
          className="browser-default"
          value={qrSize}
          onChange={(e) => setQrSize(parseInt(e.target.value))}
          style={{
            padding: "8px",
            borderRadius: "5px",
            backgroundColor: "#e6ffe6",
            color: "#004d40",
            fontWeight: "bold",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
          }}
        >
          <option value="100">{t("qrCode.small")}</option>
          <option value="200">{t("qrCode.medium")}</option>
          <option value="300">{t("qrCode.large")}</option>
        </select>
      </div>
    </Container>
  );
};

// PropTypes validation
QRCodeSizeSelector.propTypes = {
  qrSize: PropTypes.number.isRequired,
  setQrSize: PropTypes.func.isRequired,
};

export default QRCodeSizeSelector;
