import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * ZoomForm component for handling Zoom meeting URL input and generating a QR code.
 *
 * @component
 * @returns {JSX.Element} The rendered Zoom input form.
 */
export const ZoomForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [zoomURL, setZoomURL] = useState(formData.zoom?.zoomURL || "");
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.zoom?.qrCodeValue || "",
  );

  useEffect(() => {
    if (zoomURL) {
      const newQrCodeValue = generateQRCodeValue({ zoom: { zoomURL } });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("zoom", { zoomURL, qrCodeValue: newQrCodeValue });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [zoomURL, qrCodeValue, updateFormData, setQrCodeVisible]);

  const handleZoomURLChange = (e) => {
    setZoomURL(e.target.value);
  };

  return (
    <form className="row">
      <div className="input-field col s12">
        <input
          type="url"
          id="zoom-url"
          placeholder={t("form.zoomPlaceholder")}
          value={zoomURL}
          onChange={handleZoomURLChange}
          required
        />
      </div>
    </form>
  );
};
