import "materialize-css/dist/css/materialize.min.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "./App.css";

import React, { useEffect } from "react";
import QRCodeGenerator from "./components/QRCodeGenerator";
import { QRCodeProvider } from "./context/QRCodeContext";
import { LocationProvider } from "./context/LocationContext";
import Header from "./components/Header";
import Footer from "./components/Footer";
import "./i18n";
import config from "./config/config";

/**
 * Main application component for rendering the QR code generator app.
 * Provides the necessary context providers for QR code data and location data.
 *
 * @component
 * @returns {JSX.Element} The root element containing the entire app layout.
 */
function App() {
  useEffect(() => {
    const faviconLink = document.querySelector("link[rel~='icon']");
    if (faviconLink) {
      faviconLink.href = config.favicon;
    }
  }, []);
  return (
    <QRCodeProvider>
      <LocationProvider>
        <div
          style={{
            backgroundImage: `url(${config.backgroundImage})`,
          }}
        >
          <Header />
          <div className="row" style={{ marginTop: "3rem" }}>
            <div className="center-align">
              <QRCodeGenerator />
            </div>
          </div>
          <Footer />
        </div>
      </LocationProvider>
    </QRCodeProvider>
  );
}

export default App;
