/**
 * Loads a background image and draws it on the canvas.
 *
 * @param {CanvasRenderingContext2D} ctx - The 2D context of the canvas.
 * @param {string} imageUrl - The URL of the background image.
 * @param {number} width - The width of the canvas.
 * @param {number} height - The height of the canvas.
 * @returns {Promise<void>} - Resolves the promise once the image is loaded and drawn.
 */
export const drawBackgroundImage = (ctx, imageUrl, width, height) => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.src = imageUrl;
    img.onload = () => {
      ctx.drawImage(img, 0, 0, width, height);
      resolve(); // The image is loaded and drawn
    };
    img.onerror = reject; // In case of loading error
  });
};

/**
 * Applies a gradient on the canvas.
 *
 * @param {CanvasRenderingContext2D} ctx - The 2D context of the canvas.
 * @param {string} colorStart - The start color of the gradient.
 * @param {string} colorEnd - The end color of the gradient.
 * @param {number} width - The width of the canvas.
 * @param {number} height - The height of the canvas.
 */
export const applyGradient = (ctx, colorStart, colorEnd, width, height) => {
  const gradient = ctx.createLinearGradient(0, 0, width, height);
  gradient.addColorStop(0, colorStart); // Start of the gradient
  gradient.addColorStop(1, colorEnd); // End of the gradient
  ctx.fillStyle = gradient;
  ctx.fillRect(0, 0, width, height);
};
