import React, { useContext, useEffect, useState } from "react";
import "materialize-css/dist/css/materialize.min.css";
import { QRCodeContext } from "../../context/QRCodeContext";
import { generateQRCodeValue } from "../../utils/qrCodeUtils";
import { useTranslation } from "react-i18next";

/**
 * SkypeForm component for handling Skype ID input.
 *
 * @component
 * @returns {JSX.Element} The rendered Skype form.
 */
export const SkypeForm = () => {
  const { t } = useTranslation();
  const { formData, updateFormData, setQrCodeVisible } =
    useContext(QRCodeContext);

  const [skypeId, setSkypeId] = useState(formData.skype?.skypeId || "");
  const [qrCodeValue, setQrCodeValue] = useState(
    formData.skype?.qrCodeValue || "",
  );

  useEffect(() => {
    if (skypeId) {
      const newQrCodeValue = generateQRCodeValue({ skype: { skypeId } });

      if (newQrCodeValue !== qrCodeValue) {
        setQrCodeValue(newQrCodeValue);
        updateFormData("skype", { skypeId, qrCodeValue: newQrCodeValue });
      }
      setQrCodeVisible(true);
    } else {
      setQrCodeVisible(false);
    }
  }, [skypeId, qrCodeValue, updateFormData, setQrCodeVisible]);

  const handleSkypeIdChange = (e) => {
    setSkypeId(e.target.value);
  };

  return (
    <form className="row">
      <div className="input-field col s12">
        <input
          type="text"
          id="skype-id"
          placeholder={t("form.skypePlaceholder")}
          value={skypeId}
          onChange={handleSkypeIdChange}
          required
        />
      </div>
    </form>
  );
};
